body {
  text-align: center;
  background-image: radial-gradient(circle at 100% 0, #dec9e9, #fff, #dec9e9);
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  color: #fff;
  background-color: #6247aa;
  padding: 10px;
}

.container {
  max-width: 400px;
  margin: 20px auto;
}

input[type="text"] {
  border: 2px solid #815ac0;
  border-radius: 4px;
  width: 70%;
  margin-right: 5px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #815ac0;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button:hover {
  background-color: #c19ee0;
}

ul {
  padding: 0;
  list-style: none;
}

li {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 5px 0;
  padding: 10px;
}

#chatbot-container {
  background: #fff;
  border: 3px solid #815ac0;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  padding: 5px 10px;
}

.history {
  padding-bottom: 15px;
}

@media screen and (width <= 845px) {
  #chatbot-container {
    background: #fff;
    border: 3px solid #815ac0;
    width: 300px;
    padding: 5px;
    position: fixed;
    bottom: 20px;
    right: 40px;
    box-shadow: 0 4px 6px #0000001a;
  }
}

@media screen and (width <= 380px) {
  #chatbot-container {
    background: #fff;
    border: 3px solid #815ac0;
    width: 300px;
    padding: 5px;
    position: fixed;
    bottom: 20px;
    right: 40px;
    box-shadow: 0 4px 6px #0000001a;
  }

  #chat-history {
    border-bottom: 1px solid #ccc;
    height: 80px;
    margin-bottom: 10px;
    padding: 5px;
    overflow-y: auto;
  }

  h1 {
    color: #fff;
    background-color: #6247aa;
    padding: 10px;
    font-size: 20px;
  }

  h2 {
    font-size: 16px;
  }
}
